<template>
  <div id="app">
    <img src="./assets/logo.png">
    <h1></h1>
    <h2>{{msg}}</h2>
    <router-view class="view"></router-view>
    <h2>test</h2>
    <ul>
        <li>  <router-link to="/">/</router-link></li>
        <li><router-link to="/detail-page">/详情页</router-link></li>
    </ul>
  </div>
</template>

<script>
import Vue from 'vue'
import router from './api/router-config.js'
import { mapState } from 'vuex'

export default {
  name: 'app',
  router,
  data () {
    return {
      msg: '这是应用框架页'
    }
  },
  created: function () {
    
  },
  methods:{
  },

  watch: {
    loginOperate: function (newLoginOperate) {
        router.push("/login-page");
    }
  },
  computed: mapState("authorize",['loginOperate']),

};


</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



