import Vue from 'vue'
import VueRouter from 'vue-router'
import AppConfig from './api/app-config.js'
import App from './App.vue'
import store from './store'
import AxiosConfig from './api/axios-config.js'

Vue.use(AppConfig)
Vue.use(store)
Vue.use(AxiosConfig)
Vue.use(VueRouter)

new Vue({
  el: '#app',
  store:store,
  render: h => h(App)
})



