import Vue from 'vue'
import Vuex from 'vuex'
import AppConfig from '../api/app-config.js'
import authorize from './authorize'
Vue.use(Vuex)
const store =new Vuex.Store({
  modules: {
    authorize
  },
  strict: AppConfig.debug
});
export default store



